@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
.Navbar {
  display: flex;
  justify-content: space-around;
  width: 60%;
  margin-right: 50px;
  margin-left: 50px;
}

.Navbar p {
  font-size: 30px;
  height: 30px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Inactive,
.Active {
  position: relative;
}

.Inactive::after {
  content: "";
  background: black;
  position: absolute;
  bottom: 0;
  left: 15%;
  width: 0;
  height: 3px;
  transition: width 0.3s;
}

.Inactive:hover::after {
  width: 70%;
}

.Active::after {
  content: "";
  background: black;
  position: absolute;
  bottom: 0;
  left: 15%;
  width: 70%;
  height: 3px;
}

