@media only screen and (max-width: 600px) {
  /* For mobile phones: */
  .Navbar {
    justify-content: space-between;
  }

  .Navbar p {
    font-size: 6vw;
    height: 6vw;
  }
}

@media only screen and (max-width: 950px) {
  /* For mobile phones: */
  .Navbar {
    width: 100%;
    margin-right: 0;
    margin-left: 0;
    justify-content: space-around;
  }
}

.Wrapper {
  display: flex;
  justify-content: center;
  margin-top: 40px;
  width: 100%;
}

.About {
  width: 50%;
  text-align: justify;
  margin: 20px 0;
}

.Description {
  font-size: 25px;
}

.ResumeDiv {
  display: flex;
  align-items: flex-end;
  height: 30px;
}

.ResumeDiv .Icon {
  padding-bottom: 1px;
  padding-right: 5px;
}

@media only screen and (max-width: 950px) {
  /* For mobile phones: */
  .About {
    width: 100%;
    margin-left: 15px;
    margin-right: 15px;
  }

  .Description {
    font-size: 18px;
  }

  .ResumeDiv {
    justify-content: center;
  }

  .ResumeDiv a {
    font-size: 18px;
  }
}

.project-listing {
  width: 70%;
  height: calc(200px - 44px);
  padding: 20px;
  border: 2px solid black;
  display: flex;
  margin-bottom: 20px;
}

.project-listing-image {
  height: 100%;
  width: 200px;
}

.project-listing-description {
  height: 100%;
  width: 100%;
  margin-left: 20px;
}

.project-listing-description-title {
  text-transform: uppercase;
  font-size: 1.3em;
}

.project-listing-description-text {
  margin-top: 30px;
  font-size: 1.1em;
  font-weight: 500;
}

.project-listing-description-links {
  margin-top: 30px;
  display: flex;
}

.project-listing-description-links div {
  margin-right: 20px;
}

@media only screen and (max-width: 1030px) {
  .project-listing {
    height: calc(250px - 44px);
  }
}

@media only screen and (max-width: 760px) {
  .project-listing {
    min-height: calc(400px - 44px);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    overflow: hidden;
    flex-wrap: wrap;
  }

  .project-listing-image {
    height: 50%;
    max-height: 50%;
    width: 100%;
  }

  .project-listing-description {
    margin-left: 0;
    min-height: 50%;
  }

  .project-listing-description-title {
    text-align: center;
    margin-top: 10px;
  }

  .project-listing-description p,
  .project-listing-description a {
    font-size: 3vw;
  }

  .project-listing-description-text {
    text-align: justify;
  }

  .project-listing-description-text,
  .project-listing-description-links {
    margin-top: 15px;
  }
}

.Line1 {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.Line1 .InputWrapper {
  width: 48%;
  margin-top: 10px;
}

.Line1 .InputWrapper input {
  width: calc(100% - 14px);
  height: 30px;
  margin-top: 5px;
}

.Line2 {
  margin-top: 10px;
  margin-bottom: 5px;
}

.Line2 .InputWrapper {
  width: 100%;
}

.Line2 .InputWrapper textarea {
  width: calc(100% - 14px);
  height: 200px;
  margin-top: 5px;
  text-align: start;
}

.InputWrapper p {
  font-weight: normal;
}

.errorMessage {
  color: red;
  font-size: 0.9rem;
}

@media only screen and (max-width: 600px) {
  /* For mobile phones: */
  .About {
    width: 80%;
  }

  .Line1 {
    flex-wrap: wrap;
  }

  .Line1 .InputWrapper {
    max-width: 100%;
    min-width: 100%;
    justify-content: flex-start;
  }

  .Line1 .InputWrapper input {
    /* 4px de borda, 10px de padding */
    width: calc(100% - 14px);
  }

  .Line2 .InputWrapper textarea {
    /* 4px de borda, 10px de padding */
    width: calc(100% - 14px);
  }
}

@media only screen and (max-width: 950px) {
  .About {
    max-width: 80%;
  }
}

html,
body,
p,
h1,
h2 {
  margin: 0;
  padding: 0;
}

main {
  min-height: 600px;
  max-width: 100%;
  margin: 35px;
  padding-bottom: 15px;
  border: 3px solid black;
  font-family: "Montserrat";
}

p {
  color: black;
  font-weight: lighter;
  font-size: 22px;
}

a {
  color: black;
  font-weight: lighter;
  font-size: 20px;
  margin-top: 100px;
}

input,
textarea,
button {
  font-family: "Montserrat";
  font-size: 20px;
  font-weight: lighter;
  border: 2px solid black;
  padding: 5px;
  resize: none;
}

button {
  cursor: pointer;
  background-color: white;
}

input:focus,
textarea:focus {
  outline: none;
}

h2 {
  font-size: 2.5rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 10px;
}

.Header {
  display: flex;
  max-width: 100%;
}

.IntroBox {
  margin-top: -40px;
  margin-left: -3px;
  left: 35px;
  background-color: black;
  min-width: 40%;
  max-height: 100px;
  padding: 40px;
  color: white;
  text-align: center;
}

.IntroBox h1 {
  font-weight: bold;
  font-size: 36px;
  text-transform: uppercase;
  margin: 0;
}

.IntroBox h2 {
  font-weight: lighter;
  font-size: 30px;
  margin-top: 10px;
}

.Icon {
  width: 20px;
  height: 20px;
}

@media only screen and (max-width: 950px) {
  /* For mobile phones: */

  main {
    min-height: 500px;
  }

  .Header {
    display: block;
  }

  .IntroBox {
    max-width: 100%;
    margin-right: -3px;
    padding: 20px;
  }

  .IntroBox h1 {
    font-size: 26px;
  }

  .IntroBox h2 {
    font-size: 20px;
  }
}

@media only screen and (max-width: 600px) {
  p {
    font-size: 5vw;
  }

  h2 {
    font-size: 9vw;
  }
}

